import React, { useState, useEffect } from 'react';

import styles from './profileStatus.module.css';


const ProfileStatusWithHooks = ({ isMyPage, ...props }) => {
    const [editMode, setEditMode] = useState(false);
    const [status, setStatus] = useState(props.status);

    useEffect(() => {
        setStatus(props.status);
    }, [props.status])

    const outEditMode = () => {
        setEditMode(false);
        props.updateStatus(status);
    }


    return <div>
        {isMyPage ?
            !editMode
                ? <div>
                    <span onDoubleClick={() => { setEditMode(true) }} className={styles.status}>{props.status || 'set a status message'}</span>
                </div>
                : <div>
                    <input autoFocus={true} onChange={(e) => { setStatus(e.currentTarget.value) }} onBlur={outEditMode} value={status} />
                </div>
            : <div>
                {
                   props.status 
                   ? props.status.length > 100 ? props.status.slice(0,100) : props.status
                   : null
               }
            </div>
        }

    </div>

}

export default ProfileStatusWithHooks;